import React, { Component as Cp } from "react";
import TaskBar from "./taskbar/TaskBar";
import MainBackground from "./Background";
import MainManager from "../MainManager";
import TaskManager from "./taskbar/TaskManager";
export const TaskBarRef = React.createRef<TaskManager>();
export const MManagerRef = React.createRef<MainManager>();
export default class Oinkedit extends Cp<defaultReactProps>{
    render() :React.ReactNode{
        return(<>
            <div style={{
                height: "100vh",
                width: "100vw",
                display: "flex",
                flexFlow: "column nowrap"
            }}>
                <TaskBar forwardRef={TaskBarRef} />
                <MainBackground />
            </div>
            <MainManager ref={MManagerRef} />
        </>);
    }
}
//开发环境ONLY
export function export_(arg :anyObject){
    for(let i in arg) (window as anyObject)[i] = arg[i];
}