import React, { Component as Cp } from "react";
import { windowDescription } from "../MainManager";
import { taskBarHW } from "../utils/css";
import styles from "./Window.module.css";
interface Props extends defaultReactProps{
    _ :windowDescription;
};
interface State{
    isDown :boolean;
    top :number;
    left :number;
    attached_t :boolean;
    attached_b :boolean;
    attached_l :boolean;
    attached_r :boolean;
    hoveredInTaskBar :boolean;
}
export default class Window extends Cp<Props, State>{
    deltaTop :number = 0;
    deltaLeft :number = 0;
    ref = React.createRef<HTMLDivElement>();
    constructor(props :Props){
        super(props);
        const topFromProp = props._.position.top || taskBarHW.height,
              leftFromProp = props._.position.left || taskBarHW.width;
        this.state = {
            isDown: false,
            top: topFromProp, left: leftFromProp, hoveredInTaskBar: false,
            //这里元素还没有渲染，所以不能获取到height和width
            //这也意味着窗口刚打开时可能会卡在奇怪的地方，但只要拖动一下就会立刻变正常
            ...this.checkPos({top: topFromProp, left: leftFromProp}, 0, 0)
        };
    }
    /**
     * 测试一组坐标是否超出浏览器窗口界限，如果超出则拉回至合法坐标
     * @weird 怪异函数：通过修改引用类型入参返回了其他的信息。*/
    checkPos(topLeftData :{top :number, left :number}, height :number, width :number){
        var attached_t = false, attached_b = false ,attached_l = false, attached_r = false;
        const b = document.body;
        if(topLeftData.top >= b.clientHeight - height){
            attached_b = true;
            topLeftData.top = b.clientHeight - height;
        }
        if(topLeftData.left >= b.clientWidth - width - 1){ //-1是为了不让文本换行
            attached_r = true;
            topLeftData.left = b.clientWidth - width - 1;
        }
        if(topLeftData.top <= taskBarHW.height){
            attached_t = true;
            topLeftData.top = taskBarHW.height;
        }
        if(topLeftData.left <= taskBarHW.width){
            attached_l = true;
            topLeftData.left = taskBarHW.width;
        }
        return {attached_t, attached_b, attached_l, attached_r};
    }
    /**获取窗口实时宽高*/
    getHW(){
        const styles = getComputedStyle(this.ref.current!),
              height = parseFloat(styles.height) + parseFloat(styles.paddingTop) + parseFloat(styles.paddingBottom),
              width = parseFloat(styles.width) + parseFloat(styles.paddingLeft) + parseFloat(styles.paddingLeft);
        return {height, width};
    }
    syncHoverStatus = ()=>{
        this.setState({hoveredInTaskBar: this.props._.hover});
    }
    mouseMoveCB = (e :MouseEvent) :void=>{
        if(this.state.isDown) this.setState(()=>{
            const {height, width} = this.getHW(),
                  //计算坐标
                  topLeftData = {top: e.pageY - this.deltaTop, left: e.pageX - this.deltaLeft},
                  //检查坐标
                  attacheds = this.checkPos(topLeftData, height, width);
            //向公用对象同步坐标，虽然props只读，但是真的很方便……先这样吧
            this.props._.position.left = topLeftData.left;
            this.props._.position.top = topLeftData.top;
            return {...attacheds, ...topLeftData};
        });
    }
    mouseUpCB = (e :MouseEvent) :void=>{
        if(this.state.isDown){
            this.setState({isDown: false});
            document.removeEventListener("mousemove", this.mouseMoveCB);
            document.removeEventListener("mouseup", this.mouseUpCB);
            document.body.classList.remove("moving");
        }
    }
    mouseDown = (event :React.MouseEvent<HTMLDivElement, MouseEvent>) :void=>{
        if(event.currentTarget === event.target){
            this.setState({isDown: true});
            document.body.classList.add("moving");
            document.addEventListener("mousemove", this.mouseMoveCB);
            document.addEventListener("mouseup", this.mouseUpCB);
            this.deltaTop = event.pageY - this.state.top;
            this.deltaLeft = event.pageX - this.state.left;
        }
    }
    render() :React.ReactNode{
        return(
            <div
                className={`${styles.window}${this.state.hoveredInTaskBar ? ` ${styles.hoverWindow}` : ""}${this.state.attached_t ? ` ${styles.attached_t}` : ""}${this.state.attached_b ? ` ${styles.attached_b}` : ""}${this.state.attached_l ? ` ${styles.attached_l}` : ""}${this.state.attached_r ? ` ${styles.attached_r}` : ""}`}
                style={{
                    top: `${this.state.top}px`,
                    left: `${this.state.left}px`,
                    cursor: this.state.isDown ? "grabbing" : "grab",
                    maxWidth: "calc(100% - 1px)",
                    maxHeight: "calc(100% - 1px)"
                }}
                onMouseDown={this.mouseDown} ref={this.ref}>
                <div className={styles.fwinner}>
                    <div>固定测试</div>
                    {this.props.children}
                </div>
            </div>
        );
    }
}