import React, { Component as Cp } from "react";
import styles from "./Task.module.css";
interface Props extends defaultReactProps{
    label :string;
    id :string;
    toggle :Function;
    hover :Function;
    dehover :Function;
    copy :Function;
}
interface State{
    isDown :boolean;
}
export default class Task extends Cp<Props, State>{
    constructor(props :Props){
        super(props);
        this.state = {
            isDown: false
        };
    }
    mouseMoveCB = (e :MouseEvent) :void=>{
        if(this.state.isDown){
            console.log(this, "move");
        }
    }
    mouseUpCB = (e :MouseEvent) :void=>{
        if(this.state.isDown){
            this.setState({isDown: false});
            document.removeEventListener("mousemove", this.mouseMoveCB);
            document.removeEventListener("mouseup", this.mouseUpCB);
            document.body.classList.remove("moving");
        }
    }
    mouseDown = (event :React.MouseEvent<HTMLLIElement, MouseEvent>) :void=>{
        this.setState({isDown: true});
        document.body.classList.add("moving");
        document.addEventListener("mousemove", this.mouseMoveCB);
        document.addEventListener("mouseup", this.mouseUpCB);
    }
    click = ()=>{
        this.props.toggle(this.props.id);
    }
    hover = ()=>{
        this.props.hover(this.props.id);
    }
    dehover = ()=>{
        this.props.dehover(this.props.id);
    }
    render() :React.ReactNode{
        return(
            <li
                onMouseDown={this.mouseDown}
                onClick={this.click}
                onMouseOver={this.hover}
                onMouseLeave={this.dehover}
                className={`${styles.li}${false ? ` ${styles.fixed}` : ""}`}
                style={{cursor: this.state.isDown ? "grabbing" : "grab"}}>
                <div>{this.props.label}</div>
            </li>
        );
    }
}