import React, { Component as Cp, ReactElement } from "react";
import Window from "./window/Window";
import { export_, TaskBarRef } from "./majors/Oinkedit";
import { dataBase } from "./lifecycle/iniStorage";
import { randoma2z029 } from "./utils/general";
interface State{
    windows :windowInstance[];
}
export interface windowDescription{
    id :string;
    type :i18n_Sections;
    minimized :boolean;
    resizable :boolean;
    hover :boolean;
    fixed :boolean;
    appMeta :anyObject;
    position :{
        top :number | undefined;
        left :number | undefined;
    }
    size: {
        width :number | undefined;
        height :number | undefined;
    }
}
interface windowInstance{
    desc :windowDescription;
    instance :React.ReactElement; //note:这里暂时这样定义先吧
    ref :React.RefObject<Window>;
}
/**@once*/
export default class MainManager extends Cp<defaultReactProps, State>{
    //id :string = 0;
    constructor(props :defaultReactProps){
        super(props);
        this.state = {windows: []};
        //获取数据
        (async ()=>{
            this.state = {windows: (await dataBase.table("config").get("windows")).value};
        })().then(()=>{
            //this.id = setInterval(()=>{this.syncData()}, 1000) as unknown as number;
        });
        export_({openWindow: this.openWindow});
    }
//#region 同步数据（note:看看能不能做到不轮询）
    //componentWillUnmount() :void{
    //    clearInterval(this.id);
    //}
    async syncData(){
        dataBase.table("config").put({key: "windows", value: this.state.windows});
    }
//#endregion
//#region 开放给TaskBar的API
    public toggleWindow = (id :string) :boolean=>{
        console.log(id);
        const windows = this.state.windows;
        for(let i = 0; i < windows.length; i++) if(id === windows[i].desc.id){
            windows[i].desc.minimized = !windows[i].desc.minimized;
            this.forceUpdate();
            return true;
        }
        return false;
    }
    public hoverWindow = (id :string)=>{
        const windows = this.state.windows;
        for(let i = 0; i < windows.length; i++) if(id === windows[i].desc.id){
            windows[i].desc.hover = true;
            //fixme:比较粗暴
            windows[i].ref.current!.syncHoverStatus();
            return true;
        }
        return false;
    }
    public dehoverWindow = (id :string)=>{
        const windows = this.state.windows;
        for(let i = 0; i < windows.length; i++) if(id === windows[i].desc.id){
            windows[i].desc.hover = false;
            //fixme:比较粗暴
            windows[i].ref.current!.syncHoverStatus();
            return true;
        }
        return false;
    }
    public copyWindow = ()=>{
        //todo:计划中的功能
    }
//#endregion
//#region 开放给所有组件的API
    public openWindow = (
        type :i18n_Sections,
        appMeta :anyObject,
        label? :string,
        position?: {top: number, left: number},
        size?: {width :number, height :number}
    ) :string=>{
        //todo:真正地将那些app打开
        //todo:通过现有窗口决定label
        const id = randoma2z029(20), ref = React.createRef<Window>(), desc :windowDescription = {
            appMeta, id, fixed: false, minimized: false, hover: false, //todo:fixed属性未投入使用。
            position: {
                top: position?.top,
                left: position?.top
            },
            resizable: false,
            type,
            size: {
                width: size?.width,
                height: size?.height
            }
        }, newWindows = [...this.state.windows, {
            desc, ref, instance: <Window key={id} _={desc} ref={ref}><div contentEditable>
                这里以后要真正地将那些app打开
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. <br />
                Sed sed pellentesque justo. Quisque non semper lectus. Nam vel leo<br />
                vitae enim scelerisque finibus. Duis sagittis condimentum finibus. Maecenas<br />
                fringilla ligula ac sapien aliquam tincidunt. Morbi porttitor at velit vel imperdiet.<br />
                Curabitur lectus nibh, efficitur a sapien sit amet, volutpat tincidunt urna. In lacinia,<br />
                sem vel dignissim mattis, lacus turpis sodales elit, vel cursus magna dolor sit amet justo.
            </div></Window>
        }];
        TaskBarRef.current!.addTask(id, label || "[label is under construction]");
        this.setState({
            windows: newWindows
        });
        //console.log(this.state.windows);
        this.forceUpdate();
        return id;
    }
    public closeWindow = (id :string) :boolean=>{
        const windows = this.state.windows;
        for(let i = 0; i < windows.length; i++) if(id === windows[i].desc.id){
            //todo:实例收尾工作
            //通知taskbar清除task
            TaskBarRef.current!.removeTask(id);
            return true;
        }
        return false;
    }
//#endregion
    render() :React.ReactNode{
        const render :Children = [], windows = this.state.windows;
        //argument:用css隐藏好，还是条件渲染好？条件渲染能节省空间，但会完全丢失app的未保存状态！
        for(let i = 0; i < windows.length; i++) if(!windows[i].desc.minimized){
            render.push(windows[i].instance);
        }
        console.log("windows", windows, "render", render);
        return(
            <div style={{
                position: "fixed",
                width: 0,
                height: 0,
                top: 0,
                left: 0
            }}>{render}</div>
        );
    }
}