import React, { Component as Cp } from "react";
import Task from "./Task";
import { export_, MManagerRef } from "../Oinkedit";
interface State{
    tasks :taskInstance[];
}
interface taskInstance{
    id :string;
    label :string;
}
/**@once*/
export default class TaskManager extends Cp<defaultReactProps, State>{
    constructor(props :defaultReactProps){
        super(props);
        this.state = {tasks: []};
        export_({add:this.addTask});
    }
    public addTask = (id :string, label :string)=>{
        this.state.tasks.push({id, label});
        //console.log(this.state.tasks);
        this.forceUpdate();
    }
    public removeTask(id :string){
        const tasks = this.state.tasks;
        for(let i = 0; i < tasks.length; i++) if(tasks[i].id === id) this.state.tasks.splice(i, 1);
        this.forceUpdate();
    }
    toggle = (id :string)=>{
        MManagerRef.current!.toggleWindow(id);
    }
    hover = (id :string)=>{
        MManagerRef.current!.hoverWindow(id);
    }
    dehover = (id :string)=>{
        MManagerRef.current!.dehoverWindow(id);
    }
    copy = (id :string)=>{
        console.log(`${id}copy`);
        MManagerRef.current!.copyWindow();
    }
    render() :React.ReactNode{
        const taskElements :any[] = [], tasks = this.state.tasks;
        for(let i = 0; i < tasks.length; i++) taskElements.push(<Task
            id={tasks[i].id}
            label={tasks[i].label}
            key={tasks[i].id}
            toggle={this.toggle}
            hover={this.hover}
            dehover={this.dehover}
            copy={this.copy}
        />);
        console.log("taskElements", taskElements);
        return(
            <ul style={{
                height: "100%",
                flex: "auto",
                listStyle: "none",
                display: "flex",
                flexFlow: "row nowrap",
                gap: 0,
                margin: 0,
                padding: 0
            }}>{taskElements}</ul>
        );
    }
}